<template>
  <div class="catalog">
    <div>
      <van-nav-bar
        class="bar"
        title="目录"
        left-text="返回"
        left-arrow
        @click-left="onClickLeft"
        @click-right="onClickRight"
      >
        <template #right>
          <van-icon :name="show ? 'cross' : 'ellipsis'" size="18" />
        </template>
      </van-nav-bar>
      <van-popup v-model="show" position="top" :style="{ height: '171.2px'}">
        <van-grid :column-num="3">
          <van-grid-item
            v-for="(item,index) in popup"
            :key="index"
            :icon="item.icon"
            :text="item.text"
            @click="popupClick(item)"
          />
        </van-grid>
      </van-popup>
    </div>
    <div class="booklist">
      <van-list
        v-model="loadOptions.isLoading"
        :finished="loadOptions.isFinished"
        :finished-text="loadOptions.finishedText"
        @load="onLoad"
        :offset="50"
      >
        <van-cell class="total" style="color:#333730;font-weight:bold;" :title="'共'+length+'章'">
          <template #default>
            <div @click="sort" style="color:#333730;font-weight:200;">{{isSort ? '正序' : '倒序'}}</div>
          </template>
        </van-cell>
        <van-cell
          v-for="(item,index) in listData"
          :key="index"
          :title="item.title"
          @click="listClick(index,item)"
          :class="index == isIndex ? 'active' : ''"
          is-link
        />
      </van-list>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
      bookId: {},
      isIndex: 0,
      popup: [
        {
          icon: "home-o",
          text: "主页",
          route: "Home"
        },
        {
          icon: "contact",
          text: "账户",
          route: "Login"
        },
        {
          icon: "search",
          text: "搜索",
          route: "Search"
        },
        {
          icon: "bookmark-o",
          text: "书架",
          route: "Bookshelf"
        },
        {
          icon: "bar-chart-o",
          text: "排行榜",
          route: "Ranking"
        }
      ],
      listData: [],
      allListData: [],
      loadOptions: {
        //是否处于加载状态，加载过程中不触发load事件
        isLoading: true,
        //是否完成加载所有数据
        isFinished: false,
        finishedText: "没有章节可加载了"
      },
      //每次加载20条数据
      loadCount: 20,
      isSort: false,
      length: 0
    };
  },
  created() {
    this.bookId = this.$route.query;
    this.getbookChapters();
  },
  methods: {
    //返回
    onClickLeft() {
      this.$router.go(-1);
    },
    onClickRight() {
      this.show = !this.show;
    },
    popupClick(item) {
      this.$router.push({ name: item.route });
    },
    getbookChapters(isSort) {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "http://novel.kele8.cn/book-chapters/" + this.bookId.id
      })
        .then(result => {
          this.$toast.clear();
          this.length = result.data.chapters.length;
          if (Object.keys(result.data).length > 0) {
            this.allListData = result.data.chapters;
            this.allListData = this.allListData.sort(this.sortNumber);
            this.listData = this.allListData.splice(0, this.loadCount);
            if (this.allListData.length > 0) {
              this.loadOptions.isLoading = false;
            } else {
              this.loadOptions.isLoading = true;
              this.loadOptions.isFinished = true;
            }
            if (this.listData.length == 0) {
              this.loadOptions.finishedText = "";
            }
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
    sort() {
      this.isSort = !this.isSort;
      this.getbookChapters(this.isSort);
    },
    sortNumber(a, b) {
      if (this.isSort) {
        return b.order - a.order;
      } else {
        return a.order - b.order;
      }
    },
    onLoad() {
      setTimeout(() => {
        this.listData.push(...this.allListData.splice(0, this.loadCount));
        // 数据全部加载完成
        this.loadOptions.isLoading = false;
        if (this.allListData.length == 0) {
          this.loadOptions.isFinished = true;
        }
      }, 200);
    },
    listClick(index, item) {
      this.isIndex = index;
      let ids = this.bookId.id;
      this.$router.push({
        name: "Book",
        query: { isSort: this.isSort, index: index, bookId: ids }
      });
    }
  }
};
</script>

